.text-container {
    position: absolute;
    top: 55%;
    right: 0%;
    margin: auto;
    transform: translate(-0%, -50%);
}

.title {
    width: 100%;
    border-bottom: 1px solid black;
    padding-bottom: 4%;
    padding-top: 4%;
}
.title h1 {
    font-family: TeXGyreTermes;
    font-size: 66px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1;
    letter-spacing: normal;
    text-align: left;
    color: #3f3862;
}


.text p {
    margin-top: 5%;
    font-family: 'Poppins', sans-serif;
    font-size: 17.5px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: normal;
    text-align: justify;
    color: #8F9FAE;
}


@media (max-width: 1600px) {
    .title h1 {
        font-size: 60px;
    }

    .text p {
        font-size: 16px;
    }
   }
   
   @media (max-width: 1400px) {
    .title h1 {
        font-size: 44px;
    }

    .text p {
        font-size: 14px;
    }
   }
   
   @media (max-width: 992px) {
    .title h1 {
        font-size: 40px;
    }

    .text p {
        font-size: 20px;
    }
   }